<template>
	<v-card>
		<v-card-title class="py-2">Pilih Startup</v-card-title>
		<v-card-text>
			<Loader v-if="isLoading" />
			<v-data-table
				v-else
				class="mt-0"
				:headers="header"
				:items="data"
				:items-per-page="10">	
				<template v-slot:item.no="{item}">
					{{data.map(x => x.id).indexOf(item.id)+1}}
				</template>
				<template v-slot:item.startup="{item}">
					<div class="d-flex align-center">
						<v-avatar size="40" color="grey lighten-3">
							<div v-if="item.startup.logo === null">L</div>
							<img 
								v-else 
								:src="`http://progresio.id/api/public/uploads/logo/${item.startup.logo}`"  
								alt="logo startup">
						</v-avatar>
						<div class="ml-3">
							{{ item.startup.name }} <br>
							<span class="body grey--text">{{ item.startup.bidang }}</span>
						</div>
					</div>
				</template>
				<template v-slot:item.manage="{item}">
					<div class="d-flex align-center">
						<v-avatar size="40" color="grey lighten-3">
							<div v-if="item.manage.avatar === null">A</div>
							<img 
								v-else  
								:src="`http://progresio.id/api/public/uploads/profile/${item.manage.avatar}`" 
								alt="logo manage">
						</v-avatar>
						<div class="ml-3">
							{{ item.manage.name }} <br>
							<span class="body grey--text">{{ item.manage.jabatan }}</span>
						</div>
					</div>
				</template>
				<template v-slot:no-data>
					<v-alert type="info" class="mt-4">
						<div>Data tidak ditemukan</div>
					</v-alert>
				</template>				
				<template v-slot:item.actions="{ item }">
					<v-btn
						small dark
						@click="$router.push({ name: 'show-response', params: {startup_id: item.id, traction_id: traction_id} })"
						color="cyan"
						depressed>
						lihat traction
					</v-btn>
				</template>	
			</v-data-table>
		</v-card-text>
	</v-card>
</template>

<script>
  import { mapState } from 'vuex'
  import Loader from '@/components/Loader'
	export default{
		props: ['traction_id'],

		data: () => ({
			header: [
				{ text: 'No', value: 'no', sortable: false },
				{ text: 'Startup', value: 'startup', sortable: false },
				{ text: 'Category', value: 'category', sortable: false },
				{ text: 'Managed By', value: 'manage', sortable: false },
				{ text: '', value: 'actions', align: 'center', sortable: false }
			],
		}),		
    components: { Loader },
    created() {
      this.$store.dispatch('dataIncubatorStartup/getShowStartup')
      this.$store.dispatch('dataIncubatorStartup/updateIsLoading', true)
    },
    computed: {
      ...mapState('dataIncubatorStartup', ['isLoading', 'dataStartup']),
      data() {
        return this.dataStartup.map(data => {
          return {      			
						id: data.id,
						category: 'Education',
						startup: {
							logo: data.profile.logo,
							name: data.name,
							// bidang: 'Education'
						},
						manage: {
							avatar: data.profile.manager_profile_foto,
							name: data.profile.manager_name,
							// jabatan: 'CEO'
						}				
          }
        })
      }
    }
	}
</script>